.wrap-hdr {
  padding: 5px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: solid 1px #fff;
}
.wrap-hdr .nav-hdr {
  margin: 30px 0;
}
.wrap-hdr .logo .title {
  font-size: 40px;
  font-weight: bold;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
}
@media screen and (min-width: 769px) {
  .wrap-hdr {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
  }
  .wrap-hdr .nav-hdr {
    margin: 0;
  }
  .wrap-hdr .nav-hdr ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    gap: 20px;
  }
  .wrap-hdr .nav-hdr ul li a {
    text-decoration: none;
    color: #fff;
    font-family: "Rajdhani", sans-serif;
    font-weight: 500;
    font-size: 15px;
    text-transform: uppercase;
    padding: 10px 20px;
    transition: color 0.3s ease;
  }
  .wrap-hdr .nav-hdr ul li a:hover {
    color: #dc143f;
  }
}

.social {
  display: flex;
  list-style: none;
  padding: 0;
  flex-direction: row;
  gap: 10px;
}
.social li a {
  width: 30px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  border: solid 1px #fff;
  border-radius: 50%;
