}

.search-form {
  display: flex;
  align-items: center;
}
.search-form .search-field {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.search-form .search-submit {
  background-color: #3498db;
