  margin-left: 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}
.search-form .search-submit:hover {
  background-color: #2980b9;
}

.wrap-menu {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}
.wrap-menu .toggle {
  color: #fff;
  cursor: pointer;
  font-size: 30px;
}
.wrap-menu .menu {
  background-color: #161616;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition: opacity 0.3s ease-in-out;
  z-index: 1;
}
.wrap-menu .menu .close {
  padding: 10px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  border: solid 1px #fff;
  border-radius: 50%;
  cursor: pointer;
  margin: 30px;
}
.wrap-menu .menu nav {
  height: 100%;
  z-index: 10;
}
.wrap-menu .menu nav .menu-menu-1-container {
  height: 100%;
}
.wrap-menu .menu nav .menu-menu-1-container ul {
  list-style: none;
  padding: 0;
  height: 100%;
}
.wrap-menu .menu nav .menu-menu-1-container ul li {
  text-align: center;
  display: block;
}
.wrap-menu .menu nav .menu-menu-1-container ul li a {
  color: #fff;
  padding: 10px 15px;
  display: block;
  text-decoration: none;
  font-family: "Rajdhani", sans-serif;
  font-size: 15px;
  text-transform: uppercase;
}
.wrap-menu .menu-active {
  opacity: 1;
  height: 100%;
  transition: overflow 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
@media screen and (min-width: 992px) {
  .wrap-menu .toggle {
    display: none;
  }
  .wrap-menu .menu {
    display: block;
    position: relative;
    background-color: transparent;
    opacity: 1;
    height: auto;
  }
  .wrap-menu .menu .close {
    display: none;
  }
  .wrap-menu .menu nav {
    height: 100%;
  }
  .wrap-menu .menu nav .menu-menu-1-container {
    height: 100%;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul {
    display: flex;
    justify-content: space-evenly;
    margin: 0;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul li a {
    color: #fff;
    padding: 10px;
    display: block;
    font-weight: 500;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul li a:hover {
    color: #dc143f;
  }
}

